.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




/**/



* {
  color: white;
}






#username-bar {
  display: flex;
  justify-content: space-between;
}

#username-bar span {
  margin: 20px;
  font-size: larger;
  font-weight: bolder;
}



.conversations {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: #212942;
  
}

.conversation-list {
  overflow: scroll;
  background-color: #212942;
}


.conversation-option {
  padding: 20px;
  height: max-content;
  display: flex;
  flex-direction: column;
  background-color: #53659e;
  margin: 15px;
  border-radius: 10px;
  cursor: pointer;
}

.conversation-option:hover {
  background-color: #6478bb;
}

.user-name {
  font-size: larger;
  font-weight: bolder;
}

.conversation-time-stamp {
  font-size: medium;
}


#chat {
  
  width: 100%;
  height: 100vh;
  background-color: #e3ebff;
  display: flex;
  flex-direction: column-reverse;
}

#messages {
  overflow: scroll;
}


#page {
  width: 100%;
  height: 100vh;
  display: flex;

}


.message-row {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.message-row.left {
  justify-content: flex-start;
}

.message-row.left .message {
  background-color: #53659e;
}

.message {
  overflow-wrap: break-word;
  height: fit-content;
  padding: 20px;
  margin: 7px 10px;
  max-width: 300px;
  display: flex;
  background-color: #7d8fcb;
  border-radius: 15px;
  box-shadow: 0 8px 8px rgba(0, 0, 0, 0.2);
}




#chat-name {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  height: 100px;
  background-color:#212942;
}

#chat-name span {
  font-size: larger;
  font-weight: bolder;

}




.input-field {
  background-color: skyblue;
  display: flex;
  flex-direction: row;
  width: 50vw;
  
}



.write-message {
  display: flex;
  width: 100%;
  background-color: #b1b2b5;
}


input {
  padding: 15px;
  border-radius: 15px;
  width: 100%;
  margin: 10px;

  color: black;
  font-size: large ;
  border: none;
}

button {
  min-width: 100px;
  width: fit-content;
  margin: 10px;
  border-radius: 15px;
  background-color: #a5d794;
  border: none;
}

button:hover {
  background-color: #99c989;
}



#create-button {
  width: 50%;
  height: 55px;
  font-size: 20pt;
}

#change-username-button {
  font-size: large;
  padding: 15px;
}




/* HIDE THE SCROLLBAR */

/* For Webkit browsers */
::-webkit-scrollbar {
  display: none;
}

/* For IE and Edge */
-ms-overflow-style: none;

/* For Firefox */
scrollbar-width: none;










